.counter {
    width: 800px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.countlabel {
    font-size: 28px;
}

.countshow {
    font-size: 68px;
    font-weight: 700;
    color: blueviolet;
    padding: 20px;
    margin: 20px;
}

.countbtns {
    width: 800px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.countbtn {
    font-size: 16px;
    background-color: blueviolet;
    padding: 20px;
    border-radius: 16px;
    border: none;
    color: white;
    transition: 0.5s;

}

.countbtn:hover {
    filter: grayscale(0.3);
    transition: 0.5s;
}